import Vue from 'vue'

//提示组件
import {Toast} from 'vant'

//注册提示组件
Vue.use(Toast)

class VerifyForm{
    constructor(){}
    // 验证表单的方法
    verify(formData){
        for(let key in formData){
            if(!formData[key].reg.test(formData[key].value)){
                //提示错误信息
                Toast({
                    message:formData[key].errorMsg,
                    position: 'middle'
                });
                return false;
            }
        }
        return true;
    }
}

//导出表单验证模块实例
export const verifyForm = new VerifyForm();