<template>
    <div class="modify">   
        <BgBox>
            <van-field
            v-model="password.phone"
            name="手机号"
            label="手机号"
            placeholder="11位手机号"
            autocomplete="off"
            />
            <van-field
            v-model="password.newPassword"
            name="新密码"
            label="新密码"
            placeholder="6-16位密码"
            autocomplete="off"
            />
        <van-button type="primary" round color="linear-gradient(to right, #ff6034, #ee0a24)" size="large" @click="updatePassword">提交</van-button>
        </BgBox>      
    </div>
</template>

<script>
    import { verifyForm } from '../assets/js/verifyForm'
    import BgBox from "../components/BgBox.vue"
    export default {
        name: 'ModifyPassword',
        components: {
            BgBox
        },
          data() {
            return {
                password: {
                    phone: '',
                    newPassword: ''
                }   
            }
        },
        methods: {
            black(){
                this.$router.go(-1);
            },
            //重置密码
            updatePassword(){
                let o = {
                    phone: {
                        value: this.password.phone,
                        errorMsg: "手机号格式不正确",
                        reg: /^1[0-9]{10}$/,
                    },
                    newPassword: {
                        value: this.password.newPassword,
                        errorMsg: "新密码由数字字母下划线组合(6-16字符)",
                        reg: /^[A-Za-z]\w{5,15}$/,
                    },
                };
                let isPass = verifyForm.verify(o);

                if(isPass){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0,
                    });
                    this.axios({
                        method: 'POST',
                        url: '/retrievePassword',
                        data: {
                            appkey: this.appkey,
                            phone: this.password.phone,
                            password: this.password.newPassword
                        }
                    }).then(res => {
                        this.$toast.clear();
                        if(res.data.code == 'L001'){
                            this.$router.push({ name: "Login" });
                        }                             
                        this.$toast(res.data.msg);
                    }).catch(err => {
                        this.$toast.clear();
                    })
                }                
            },
        }     
    }
</script>

